/**
 * ErrorPage Component
 *
 * A comprehensive error page component that displays different types of errors
 * with consistent styling following the project's design system.
 *
 * Features:
 * - Different error states (404, 500, network, auth, etc.)
 * - Consistent branding with PMS logo
 * - Action buttons for recovery
 * - Responsive design
 * - Accessibility support
 *
 * @component
 * @example
 * ```tsx
 * // 404 Not Found
 * <ErrorPage
 *   status={404}
 *   message="The page you're looking for doesn't exist."
 * />
 *
 * // Server Error with custom action
 * <ErrorPage
 *   status={500}
 *   message="Something went wrong on our end."
 *   title="Oops! Server Error"
 *   actionText="Try Again"
 *   onAction={() => retryOperation()}
 * />
 *
 * // Network Error
 * <ErrorPage
 *   status={0}
 *   message="Unable to connect to the server."
 *   showBackButton={false}
 * />
 * ```
 */

import { Alert, AlertDescription } from "@/components/ui/alert";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  AlertTriangle,
  ArrowLeft,
  FileX,
  Home,
  RefreshCw,
  Server,
  Shield,
  Wifi,
} from "lucide-react";
import { useRouter } from "next/navigation";

interface ErrorPageProps {
  /** Error message to display */
  message: string;
  /** HTTP status code */
  status: number;
  /** Optional custom title */
  title?: string;
  /** Optional action button text */
  actionText?: string;
  /** Optional action callback */
  onAction?: () => void;
  /** Whether to show the back button */
  showBackButton?: boolean;
}

/**
 * Get error icon based on status code
 */
const getErrorIcon = (status: number) => {
  switch (status) {
    case 404:
      return <FileX className="h-16 w-16 text-muted-foreground" />;
    case 500:
    case 502:
    case 503:
      return <Server className="h-16 w-16 text-muted-foreground" />;
    case 401:
    case 403:
      return <Shield className="h-16 w-16 text-muted-foreground" />;
    case 0:
      return <Wifi className="h-16 w-16 text-muted-foreground" />;
    default:
      return <AlertTriangle className="h-16 w-16 text-destructive" />;
  }
};

/**
 * Get error title based on status code
 */
const getErrorTitle = (status: number, customTitle?: string): string => {
  if (customTitle) return customTitle;

  switch (status) {
    case 404:
      return "Page Not Found";
    case 401:
      return "Unauthorized Access";
    case 403:
      return "Access Forbidden";
    case 500:
      return "Server Error";
    case 502:
      return "Bad Gateway";
    case 503:
      return "Service Unavailable";
    case 0:
      return "Connection Error";
    default:
      return "Something Went Wrong";
  }
};

/**
 * Get suggested action based on error type
 */
const getSuggestedAction = (status: number): string => {
  switch (status) {
    case 404:
      return "Go back to the homepage or check the URL";
    case 401:
      return "Please log in to continue";
    case 403:
      return "Contact your administrator for access";
    case 500:
    case 502:
    case 503:
      return "Try refreshing the page or contact support";
    case 0:
      return "Check your internet connection and try again";
    default:
      return "Try refreshing the page or contact support";
  }
};

/**
 * ErrorPage - Comprehensive error display component
 *
 * Displays various types of errors with consistent styling and user-friendly
 * messaging. Includes recovery actions and follows the project's design system.
 */
const ErrorPage = ({
  message,
  status,
  title,
  actionText,
  onAction,
  showBackButton = true,
}: ErrorPageProps) => {
  const router = useRouter();

  const errorTitle = getErrorTitle(status, title);
  const suggestedAction = getSuggestedAction(status);
  const errorIcon = getErrorIcon(status);

  const handleGoHome = () => {
    router.push("/");
  };

  const handleGoBack = () => {
    router.back();
  };

  const handleRefresh = () => {
    window.location.reload();
  };

  const handleCustomAction = () => {
    if (onAction) {
      onAction();
    } else {
      handleGoHome();
    }
  };

  return (
    <div className="flex items-center justify-center p-4">
      <Card className="w-full max-w-2xl">
        <CardHeader className="text-center space-y-4">
          {/* Brand Logo */}
          <div className="flex items-center justify-center">
            <div className="h-16 w-16 rounded-lg bg-primary flex items-center justify-center">
              <span className="text-primary-foreground font-bold text-xl">
                PMS
              </span>
            </div>
          </div>

          {/* Error Icon */}
          <div className="flex justify-center">{errorIcon}</div>

          {/* Error Title */}
          <div className="space-y-2">
            <CardTitle className="text-2xl font-bold">{errorTitle}</CardTitle>
            <CardDescription className="text-base">{message}</CardDescription>
          </div>
        </CardHeader>

        <CardContent className="space-y-6">
          {/* Error Details */}
          <Alert>
            <AlertTriangle className="h-4 w-4" />
            <AlertDescription>{suggestedAction}</AlertDescription>
          </Alert>

          {/* Action Buttons */}
          <div className="flex flex-col sm:flex-row gap-3">
            {/* Primary Action */}
            <Button onClick={handleCustomAction} className="flex-1" size="lg">
              <Home className="mr-2 h-4 w-4" />
              {actionText || "Go to Homepage"}
            </Button>

            {/* Secondary Actions */}
            <div className="flex gap-2">
              {showBackButton && (
                <Button variant="outline" onClick={handleGoBack} size="lg">
                  <ArrowLeft className="mr-2 h-4 w-4" />
                  Go Back
                </Button>
              )}

              <Button variant="outline" onClick={handleRefresh} size="lg">
                <RefreshCw className="mr-2 h-4 w-4" />
                Refresh
              </Button>
            </div>
          </div>

          {/* Additional Help */}
          <div className="text-center">
            <p className="text-sm text-muted-foreground">
              If the problem persists, please contact support.
            </p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default ErrorPage;
