import { GenericResponse } from "@/lib/api/baseApi";
import { useMutation, useQueryClient } from "@tanstack/react-query";
import { toast } from "sonner";
import { QueryKeyLiteral } from "./useSkillQuery";

type MutationOptions<T, R> = {
  onClose?: () => void;
  reset?: () => void;
  mutationFn: (data: T) => Promise<GenericResponse<R>>;
  queryKey?: QueryKeyLiteral[];
};

export function useSkillMutation<T, R>({
  mutationFn,
  onClose,
  reset,
  queryKey,
}: MutationOptions<T, R>) {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn,

    onSuccess: (res) => {
      if (res.success) {
        // Invalidate client queries to refresh the list
        if (queryKey) queryClient.invalidateQueries({ queryKey });
        // Close the dialog/modal
        reset?.();
        onClose?.();
        return;
      }

      // Handle API error response
      reset?.();
      toast.error(res.message);
      onClose?.();
    },
  });
}
